













import store from "@/store";
import Vue from "vue";
import Component from "vue-class-component";
import AdvertisementViewComponent from "@/x-vue/components/advertisement/AdvertisementView.vue";
import { ApiService } from "@/x-vue/services/api.service";
import { AdvertisementModel } from "@/x-vue/services/interfaces";
import { XHelper } from "@/x-vue-helper/x-helper";

@Component({
  components: {
    AdvertisementViewComponent,
  },
})
export default class AdvertisementView extends Vue {
  advertisement = new AdvertisementModel();
  loading = false;

  mounted(): void {
    this.loadAdvertisement();
  }

  async loadAdvertisement(): Promise<void> {
    this.loading = true;
    try {
      this.advertisement = await ApiService.instance.advertisementGet({
        idx: this.$route.params.idx,
      });
      store.state.currentCategory = this.advertisement.subcategory;
      this.loading = false;
    } catch (e) {
      XHelper.instance.error(e);
      this.loading = false;
    }
  }
}
